import React, { useState, useEffect } from 'react';
import {
	Nav,
	NavbarContainer,
	NavLogo,
	NavImg,
	MobileIcon,
	NavMenu,
	NavItem,
	NavLinks
} from './NavbarElements';
import { VscThreeBars } from 'react-icons/vsc';
import { animateScroll as scroll } from 'react-scroll';

const Navbar = ({ toggle, home }) => {
	const toggleHome = () => {
		scroll.scrollToTop();
	}

	const [scrollNav, setScrollNav] = useState(false);

	const changeNav = () => {
		if(window.scrollY >= 800) {
			setScrollNav(true);
		} else {
			setScrollNav(false);
		}
	}

	useEffect(() => {
		window.addEventListener('scroll', changeNav);
	}, [])

  return (
    <>
			<Nav scrollNav={scrollNav} home={home} >
				<NavbarContainer>
					<NavLogo to='/' onClick={toggleHome} >
						<NavImg src={require('../../images/logo_white.svg').default} />
					</NavLogo>
					<MobileIcon onClick={toggle} >
						<VscThreeBars />
					</MobileIcon>
					<NavMenu>
						<NavItem>
							<NavLinks
								to='about'
								smooth={true}
								duration={750}
								spy={true}
								exact='true'
								offset={-80}
							>
								About
							</NavLinks>
						</NavItem>
						<NavItem>
						<NavLinks
								to='projects'
								smooth={true}
								duration={750}
								spy={true}
								exact='true'
								offset={-80}
							>
								Projects
							</NavLinks>
						</NavItem>
					</NavMenu>
				</NavbarContainer>
			</Nav>
		</>
  )
}

export default Navbar
